@mixin sguare($size) {
  width: $size;
  height: $size;
}

@mixin circle($size) {
  @include sguare($size);
  border-radius: 50%;
  border: 5px double #dfab01;
}

@mixin smallSguare($size) {
  width: $size;
  height: $size;
}

@mixin smallCircle($size) {
  @include smallSguare($size);
  border-radius: 50%;
  border: 3px solid $color-clockwise-second;
}
